#mainNavbar{
    background-color: rgba(16,52,166,0.79);
    width: 100%;
    margin-top:0.15em;
}


.mainContainer{
    width: 100%;
}

.mainRow{
    width: 100%;
}


.mainButton{
    background-color: rgba(100, 148, 237,0.79);
    justify-content: center;
    border-radius: 15px;
}

.notDone{
    background-color: rgba(192, 70, 94,0.8);
}
.notDone:hover{
    background-color: rgba(192, 70, 94,0.8);
}


@media only screen and (min-width: 1201px){
    .mainContainer{
        font-size: 20px;
    }

    .mainButton{
        font-size: 1.3em;
        width: 4.8em;
    }
}

@media only screen and (max-width: 1200px) and (min-width: 1025px){
    .mainContainer{
        font-size: 18px;
    }
    .mainButton{
        font-size: 1.8em;
        width: 4.8em;
    }
}

@media only screen and (max-width: 1024px) and (min-width: 769px){
    .mainContainer{
        font-size: 12px;
    }
    .mainButton{
        font-size: 1.8em;
        width: 4.8em;
    }
}

@media only screen and (max-width: 768px) and (min-width: 481px){
    .mainContainer{
        font-size: 10px;
    }
    .mainButton{
        width: 4.8em;
    }
}

@media only screen and (max-width: 480px) and (min-width: 320px){
    .mainContainer{
        font-size: 5.15px;
    }
    .mainButton{
        font-size: 1.2em;
        width: 6.7em;
    }
}


@media only screen and (max-width: 319px) and (min-width: 100px){
    .mainContainer{
        font-size: 3.5px;
    }
    .mainButton{
        font-size: 1.05em;
        width: 10em;
    }
}
